<template>
	<div class="test">
		<p 
      v-for="(value,index) in nav"
      v-bind:title="value"
      :key="value"
      :class='{on:index==curIndex}'
      v-on:click='show(index)'>
       	 {{index}}--{{value}}
    </p>   
    <button @click="isShow=!isShow">toggle</button>
    <!-- <p v-show="isShow">aaa</p> -->
    <p v-if="isShow">123</p>
    <p v-else>456</p>
    <p :style="{'margin-left':dbCurNum}">abcd</p>
	</div>
</template>
<script>
	export default{
		name: 'app',
    data (){
      return{
        nav:['111','222','333'],
        curIndex:0,
        isShow:true
      }
    },
    computed:{
      dbCurNum(){
        return this.curIndex*10+'%'
      }
    },
    methods:{
      show:function(index){
        this.curIndex=index
      }
    }
	}
</script>
<style>
	#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }
  .on{

    font-size: 50px;
  }
</style>


